import React, { useState } from 'react';
import { Card, Table } from 'react-bootstrap';

// import {lang, doc} from '../../lang/lang';
const List1 = ({ title, campos, valores, tipos }) => {
    var rows = [];
    for( let i = 0; i < campos.length ; i++ ){
        let img1 = null
        if( tipos[i] === 'img' ){
            if( valores[i] !== null )
                img1 = require('../../assets/images/zona/' +valores[i]).default
            else
                img1 = require('../../assets/images/no_image.png').default
            rows.push(<tr key={i}><th scope="row">{campos[i]}</th><td style={{textAlign: 'left'}}><img src={img1} alt={valores[i]} style={{height: 5.0+'em', width: 5.0+'em'}} /></td></tr>);
        }else
            rows.push(<tr key={i}><th scope="row">{campos[i]}</th><td>{valores[i]}</td></tr>);
    }
    return (
        <React.Fragment>
            <Card>
                <Card.Header>
                    <Card.Title as="h5">{title}</Card.Title>
                </Card.Header>
                <Card.Body>
                    <Table striped responsive>
                        <tbody>
                            {rows}
                        </tbody>
                    </Table>
                </Card.Body>
            </Card>
        </React.Fragment>
    );
};
export default List1;